}
nav .logo {
    width: 96px;
    height: 24px;
}
nav .profile {
    width: 32px;
    height: 32px;
    position: absolute;
    right: 0;
}

.search-text {
    display: block;
    font-size: large;
}
.search-text i {
    margin-right: 2rem;
}
.hr-header {
    margin-top: 0px;
    margin-bottom: 10px;
}
.search-text input {
    background-color: #000;
    border: #000;
    /*width: 75px;*/ /* non-optimal */
}
.search-text input:focus {
outline-width: 0;
}
.search-text input::-webkit-input-placeholder {
    color: #FFF;
}
.search-text input::-ms-input-placeholder {
    color: #FFF;
}
.search-text input::placeholder {
    color: #FFF;
}
input[type="text"] {
    font-size: 1.3333333333333333em;
    font-weight: 900;
}
.page-footer {
