/*
* Card-specific styles
*/
.card {
width: 34rem;
height: 19rem;
margin: 0 auto;
overflow: hidden;
position: relative;
margin-bottom: 3rem;
}
.card .card-right {
    width: 25rem;
    float: right;
}
.card .card-left {
    float: left;
    position: relative;
    top: 12rem;
    left: 2rem;
}
.card .card-title {
    font-size: 2rem;
    margin-bottom: 0px;
    font-weight: bold;
}
.card hr {
    width: 100%;
    float: right;
    color: #fff;
    opacity: 50%;
}
.card .card-address {
    color: #8094a4;
}
.card .card-title, .card .card-address, .card .card-nav {
    margin-left: 1rem;
}
.card .card-logo-img {
    width: 6rem;
    height: 5rem;
}
.card .card-favorite {
    width: 3rem;
    height: 3rem;
    margin-right: 1rem;
    margin-top: 1rem;
    position: absolute;
    margin-left: 20rem;
}
.card .card-right-bottom {
    top: 10rem;
    position: relative;
}
.card .layer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.card-nav li {
    display: inline-block;
    margin-right: 1.5rem;
    border-right:1px solid #CCC;
    padding-right: 1.5rem;
    text-transform: uppercase;
    font-size: .8rem;
}
.card-nav li:last-child {
    border-right: none;
    padding-right: 0rem;
    margin-right: 0rem;
}
.card hr {
    margin-top: 0px;
    margin-bottom: 0px;
    opacity: .5;
}
.card-nav {
    margin-top: .25rem;
}
.card-nav img {
width: 1rem;
margin-left: .5rem;
vertical-align: top;
}
